import Vue from 'vue';
import Vuex from 'vuex';
import VueJwtDecode from 'vue-jwt-decode';
import VueCookies from 'vue-cookies';

import { authService, ticketService, userService } from '../services';
import router from '../router';

Vue.use(Vuex);

const hasUser = JSON.parse(localStorage.getItem('user'));

export default new Vuex.Store({
  state: {
    status: { error: {}, },
    user: hasUser ? { status: { signedIn: true }, user: hasUser }
               : { status: {}, user: null },
    users: {
      dataList: [],
    },
    tickets: {
      dataList: [],
      aTicket: {
        data: {},
      },
      add: {},
    },
  },
  getters: {
  },
  mutations: {
    // signin - signup - signout
    signinRequest(state, user) {
      state.status = { signingIn: true };
      state.user = user;
    },
    signinSuccess(state, user) {
      state.status = { signedIn: true };
      state.user = user;
    },
    signinFailure(state, error) {
      state.status = { error, signedIn: false };
      state.user = null;
    },

    signout(state) {
      state.status = {};
      state.user = null;
    },
    
    signupRequest(state) {
      state.status = { signingUp: true };
    },
    signupSuccess(state) {
      state.status = {signedUp: true};
    },
    signupFailure(state, error) {
      state.status = {};
      state.errorResponse = error;
    },

    // User
    getUserListRequest(state) {
      state.users = { error: false, loading: true };
    },
    getUserListSuccess(state, payload) {
      state.users.loading = false;
      state.users.dataList = payload.data;
    },
    getUserListFailure(state, error) {
      state.users = {
        error: true,
        errorMessage: error,
        loading: false,
      };
    },

    // Ticket
    getTicketListRequest(state) {
      state.tickets = { error: false, loading: true };
    },
    getTicketListSuccess(state, payload) {
      state.tickets.loading = false;
      state.tickets.dataList = payload.data;
    },
    getTicketListFailure(state, error) {
      state.tickets = {
        error: true,
        errorMessage: error,
        loading: false,
      };
    },

    getTicketByIdRequest(state) {
      state.tickets.aTicket = { error: false, loading: true };
    },
    getTicketByIdSuccess(state, payload) {
      state.tickets.aTicket = {
        loading: false,
        data: payload
      }
    },
    getTicketByIdFailure(state, error) {
      state.tickets.aTicket = {
        error: true,
        errorMessage: error,
        loading: false,
      };
    },

    createTicketRequest(state) {
      state.tickets.add = { error: false, loading: true };
    },
    createTicketSuccess(state, payload) {
      console.log(payload);
      state.tickets.add = {
        loading: false,
        message: payload
      }
    },
    createTicketFailure(state, error) {
      state.tickets.add = {
        error: true,
        errorMessage: error,
        loading: false,
      };
    },

    updateTicketRequest(state) {
      state.tickets.update = { error: false, loading: true };
    },
    updateTicketSuccess(state, payload) {
      console.log(payload);
      state.tickets.update = {
        loading: false,
        message: payload
      }
    },
    updateTicketFailure(state, error) {
      state.tickets.update = {
        error: true,
        errorMessage: error,
        loading: false,
      };
    },

    updateTicketStatusRequest(state) {
      state.tickets.updateStatus = { error: false, loading: true };
    },
    updateTicketStatusSuccess(state, payload) {
      console.log(payload);
      state.tickets.updateStatus = {
        loading: false,
        message: payload
      }
    },
    updateTicketStatusFailure(state, error) {
      state.tickets.updateStatus = {
        error: true,
        errorMessage: error,
        loading: false,
      };
    },
  },
  actions: {
    // signin - signout - signup
    signin({ commit }, { email, password }) {
      commit('signinRequest', { email });
      authService.signin(email, password)
        .then(
          user => {
            commit('signinSuccess', user);
            if (user.token) {
              localStorage.setItem('user', JSON.stringify(user));
              const tokenDecode = VueJwtDecode.decode(user.token);
              VueCookies.set('token-expire', 1, new Date(tokenDecode.exp * 1000));
            }
            router.push('/');
          },
          error => {
            commit('signinFailure', error);
          }
        );
    },
    signout({ commit }) {
      localStorage.removeItem('user');
      VueCookies.remove('token-expire');
      commit('signout');
      router.push('/signin');
      authService.signout();
    },
    signup({ commit }, user) {
      commit('signupRequest', user);
  
      authService.signup(user)
        .then(
          user => {
            commit('signupSuccess', user);
            router.push('/signin');
          },
          error => {
            commit('signupFailure', error);
          }
        );
    },

    // User
    getUserList({ commit }) {
      commit('getUserListRequest');

      userService.getUserList()
        .then(
          payload => {
            commit('getUserListSuccess', payload);
          },
          error => {
            commit('getUserListFailure', error);
          }
        );
    },
    
    // Ticket
    getTicketList({ commit }) {
      commit('getTicketListRequest');

      ticketService.getTicketList()
        .then(
          payload => {
            commit('getTicketListSuccess', payload);
          },
          error => {
            commit('getTicketListFailure', error);
          }
        );
    },
    getTicketById({ commit }, id) {
      commit('getTicketByIdRequest');

      ticketService.getTicketById(id)
        .then(
          response => {
            commit('getTicketByIdSuccess', response);
          },
          error => {
            commit('getTicketByIdFailure', error);
          }
        );
    },
    createTicket({ commit }, ticket) {
      commit('createTicketRequest');

      ticketService.createTicket(ticket)
        .then(
          response => {
            commit('createTicketSuccess', response);
            router.push('/');
          },
          error => {
            commit('createTicketFailure', error);
          }
        );
    },
    updateTicket({ commit }, ticket) {
      commit('updateTicketRequest');

      ticketService.updateTicket(ticket)
        .then(
          response => {
            commit('updateTicketSuccess', response);
          },
          error => {
            console.log(error);
            commit('updateTicketFailure', error);
          }
        );
    },
    updateTicketStatus({ commit }, ticket) {
      commit('updateTicketStatusRequest');

      ticketService.updateTicketStatus(ticket)
        .then(
          response => {
            commit('updateTicketStatusSuccess', response);
          },
          error => {
            console.log(error);
            commit('updateTicketStatusFailure', error);
          }
        );
    }
    
  },
  modules: {
  }
})
