import { ENV } from '../config';
import { authHeader } from '../helper';

function handleResponse(res) {
    return res.text().then(text => {
        const data = text && JSON.parse(text);
        if (!res.ok) {
            const error = { status: 401, statusText: "UNAUTHORIZED", message: data && data.msg };
            return Promise.reject(error);
        }

        return data;
    });
}

function signin (email, password) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password })
    };

    return fetch(ENV.API_URL + `/signin`, requestOptions)
        .then(handleResponse)
}

function signout() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(ENV.API_URL + `/signout`, requestOptions).then(handleResponse);
}

function signup(user) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(user)
    };

    return fetch(ENV.API_URL + `/signup`, requestOptions).then(handleResponse);
}

export const authService = {
    signin,
    signout,
    signup,
};