<template>
	<div class="ticket-form-create-page card card-body bg-light">
		<h2>Create a task</h2>
		<form @submit.prevent="handleSubmit">
			<div class="form-group row">
				<label for="name" class="col-sm-2 col-form-label text-right">Title *:</label>
				<div class="col-sm-10">
					<input type="text" v-model="name" id="name" class="form-control" :class="{ 'is-invalid': submitted && !name }" />
					<div v-show="submitted && !name" class="invalid-feedback">Title is required</div>
				</div>
			</div>
			<div class="form-group row">
				<label for="description" class="col-sm-2 col-form-label text-right">Description *:</label>
				<div class="col-sm-10">
					<textarea v-model="description" id="description" class="form-control" :class="{ 'is-invalid': submitted && !description }"></textarea>
					<div v-show="submitted && !description" class="invalid-feedback">Description is required</div>
				</div>
			</div>
			<div class="form-group row">
				<label for="date_of_submission" class="col-sm-2 col-form-label text-right">Date of submission</label>
				<div class="col-sm-10 text-left">
					<date-picker v-model="date_of_submission" type="date" format="DD/MM/YYYY" placeholder="Select date" id="date_of_submission"></date-picker>
				</div>
			</div>
			<div class="form-group row">
				<label for="assignee" class="col-sm-2 col-form-label text-right">Assignee</label>
				<div class="col-sm-4">
					<v-select :loading="users.loading" :options="users.dataList" v-model="assignee" id="assignee" label="email"></v-select>
				</div>
			</div>
			<div class="form-group">
				<router-link to="/" class="btn btn-link">CANCEL</router-link>
				<button class="btn btn-primary">CREATE</button>
			</div>
		</form>
	</div>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import vSelect from "vue-select";
import DatePicker from 'vue2-datepicker';

import { convertTimestamp } from '../helper';

export default {
	name: 'TicketForm',
	data () {
		return {
			name: '',
			description: '',
			assignee: {},
			date_of_submission: '',
			submitted: false,
		}
	},
	components: {
		vSelect,
		DatePicker
	},
	computed: {
		...mapState(['users'])
	},
	created () {
		this.getUserList();
	},
	methods: {
		...mapActions(['createTicket', 'getUserList']),
		handleSubmit (e) {
			e.preventDefault();
			this.submitted = true;
			const { name, description, assignee, date_of_submission } = this;

			if (name && description) {
				this.createTicket({ name, description, date_of_submission: convertTimestamp(date_of_submission), assign_user_id: assignee.id })
			}
		}
	}
}
</script>