<template>
  <div class="ticket-view-page">
      This is ticket view page
      {{aTicket}}
  </div>
</template>

<script>
import { mapActions, mapState  } from 'vuex';

export default {
  name: 'TicketView',
  data: function() {
    return {}
  },
  components: {
  },
  created: function () {
      if (this.$route.params && this.$route.params.id) {
        this.getTicketById(this.$route.params.id);
      }
	},
  computed: {
    ...mapState({
      aTicket: (state) => state.tickets.aTicket ? state.tickets.aTicket : {},
    }),
  },
  methods: {
    ...mapActions(["getTicketById"]),
  }
}
</script>