<template>
	<div class="signup-page card card-body bg-light container">
		<h2>Sign Up</h2>
		<form @submit.prevent="handleSubmit" class="justify-content-md-center">
			<div class="form-group row">
				<label for="username" class="col-sm-3 col-md-4 col-form-label text-right">Username *:</label>
				<div class="col-sm-9 col-md-4">
					<input type="text" v-model="username" id="username" class="form-control" :class="{ 'is-invalid': submitted && !username }" />
					<div v-show="submitted && !username" class="invalid-feedback">Username is required</div>
				</div>
			</div>
			<div class="form-group row">
				<label for="email" class="col-sm-3 col-md-4 col-form-label text-right">Email *:</label>
				<div class="col-sm-9 col-md-4">
					<input type="email" v-model="email" id="email" class="form-control" :class="{ 'is-invalid': submitted && !email }" />
					<div v-show="submitted && !email" class="invalid-feedback">Email is required</div>
				</div>
			</div>
			<div class="form-group row">
				<label for="password" class="col-sm-3 col-md-4 col-form-label text-right">Password *:</label>
				<div class="col-sm-9 col-md-4">
					<input type="password" v-model="password" id="password" class="form-control" :class="{ 'is-invalid': submitted && !password }" />
					<div v-show="submitted && !password" class="invalid-feedback">Password is required</div>
				</div>
			</div>
			<div class="form-group row">
				<label for="repassword" class="col-sm-3 col-md-4 col-form-label text-right">RePassword *:</label>
				<div class="col-sm-9 col-md-4">
					<input type="password" v-model="repassword" id="repassword" class="form-control" :class="{ 'is-invalid': (submitted && !repassword) || (repassword !== password) }" />
					<div v-if="submitted && !repassword" class="invalid-feedback">RePassword is required</div>
					<div v-else-if="repassword !== password" class="invalid-feedback">RePassword is not equal password</div>
				</div>
			</div>
			<div class="form-group">
				<router-link to="/" class="btn btn-link">CANCEL</router-link>
				<button class="btn btn-primary">Sign Up</button>
			</div>
		</form>
	</div>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  	name: 'Signup',
  	data () {
		return {
			username: '',
			email: '',
			password: '',
			repassword: '',
			submitted: false,
		}
	},
	computed: {
		...mapState(['status'])
	},
	methods: {
		...mapActions(['signup']),
		handleSubmit(e) {
			e.preventDefault();
			this.submitted = true;
			const { username, email, password, repassword } = this;
			if (username && email && password && repassword) {
				this.signup({ username, email, password, repassword });
			}
		}
	}
}
</script>
