import { authHeader } from '../helper';
import { ENV } from '../config';

function handleResponse(res) {
    return res.text().then(text => {
        const data = text && JSON.parse(text);
        if (!res.ok) {
            const error = (data && data.message) || res.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}

function getTicketList() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(ENV.API_URL + `/ticket/list`, requestOptions).then(handleResponse);
}

function getTicketById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(ENV.API_URL + `/ticket/view/${id}`, requestOptions).then(handleResponse);
}

function createTicket(ticket) {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(ticket)
    };

    return fetch(ENV.API_URL + `/ticket/add`, requestOptions).then(handleResponse);
}

function updateTicket(ticket) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(ticket)
    };

    return fetch(ENV.API_URL + `/ticket/update/${ticket.id}`, requestOptions).then(handleResponse);
}

function updateTicketStatus(ticket) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(ticket)
    };
    
    return fetch(ENV.API_URL + `/ticket/update/${ticket.id}/status`, requestOptions).then(handleResponse);
}

export const ticketService = {
    getTicketList,
    getTicketById,
    createTicket,
    updateTicket,
    updateTicketStatus,
};