import Vue from 'vue'
import VueRouter from 'vue-router'
import Signin from '../views/Signin.vue'
import Signup from '../views/Signup.vue'
import TicketList from '../views/TicketList.vue'
import TicketView from '../views/TicketView.vue'
import TicketFormCreate from '../views/TicketFormCreate.vue'
import PageNotFound from '../views/PageNotFound.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'TicketList',
    component: TicketList
  },
  {
    path: '/ticket/create',
    name: 'TicketFormCreate',
    component: TicketFormCreate
  },
  // {
  //   path: '/ticket/edit/:id',
  //   name: 'TicketForm',
  //   component: TicketForm
  // },
  {
    path: '/ticket/:id',
    name: 'TicketView',
    component: TicketView
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/signin',
    name: 'Signin',
    component: Signin
  },
  {
    path: '/signup',
    name: 'Signup',
    component: Signup
  },
  { 
    path: "*", 
    name: 'PageNotFound',
    component: PageNotFound 
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  // redirect to signin page if not signin and trying to access a restricted page
  const publicPages = ['/signin', '/signup'];
  const authRequired = !publicPages.includes(to.path);
  const signedIn = localStorage.getItem('user');

  if (authRequired && !signedIn) {
    return next('/signin');
  }

  next();
})

export default router
