<template>
  <div class="ticket-list-page card card-body bg-light">
    <div class="loading" v-if="tickets.loading">
      <loading :active.sync="tickets.loading" :is-full-page="true"></loading>
    </div>
    <div class="table" v-else-if="!tickets.loading">
      <vue-bootstrap4-table :rows="tickets.dataList" :classes="classes" :columns="columns" :config="config" :actions="actions" @on-add-task="onAddTask">
        <template slot="date_of_submission" slot-scope="props">
          <template v-if="!isAdmin">
            {{ props.cell_value | moment("DD/MM/YYYY") }}
          </template>
          <template v-else>
            <date-picker v-on:change="editDateOfSubmission($event, props.row)" type="date" format="DD/MM/YYYY" :disabled="!isAdmin">
              <template slot="input">
                {{ props.cell_value | moment("DD/MM/YYYY") }}
              </template>
              <template slot="icon-calendar">
                <i class="mx-icon-calendar">
                  <svg v-if="isAdmin" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024"><path d="M940.218182 107.054545h-209.454546V46.545455h-65.163636v60.50909H363.054545V46.545455H297.890909v60.50909H83.781818c-18.618182 0-32.581818 13.963636-32.581818 32.581819v805.236363c0 18.618182 13.963636 32.581818 32.581818 32.581818h861.090909c18.618182 0 32.581818-13.963636 32.581818-32.581818V139.636364c-4.654545-18.618182-18.618182-32.581818-37.236363-32.581819zM297.890909 172.218182V232.727273h65.163636V172.218182h307.2V232.727273h65.163637V172.218182h176.872727v204.8H116.363636V172.218182h181.527273zM116.363636 912.290909V442.181818h795.927273v470.109091H116.363636z"></path></svg>
                </i>
              </template>
            </date-picker>
          </template>
        </template>

        <template slot="name" slot-scope="props">
          <div class="text-left">
            <i v-if="isAdmin" v-on:click="onUpdateIsShow($event, props.row)" :style="{ color: props.row.isShow == 1 ? 'green' : 'grey', fontSize: '1.5em', margin: '0 10px' }">
              <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-check2-circle" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                <path fill-rule="evenodd" d="M15.354 2.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3-3a.5.5 0 1 1 .708-.708L8 9.293l6.646-6.647a.5.5 0 0 1 .708 0z"/>
                <path fill-rule="evenodd" d="M8 2.5A5.5 5.5 0 1 0 13.5 8a.5.5 0 0 1 1 0 6.5 6.5 0 1 1-3.25-5.63.5.5 0 1 1-.5.865A5.472 5.472 0 0 0 8 2.5z"/>
              </svg>
            </i>

            <template v-if="!isAdmin">
              {{props.cell_value}}
            </template>
            <template v-else>
              <input :value="props.cell_value" class="form-control no-border" :disabled="!isAdmin" type="text" v-on:blur="onChangeTittle($event, props.row)"  />
            </template>

            <span class="ticket-detail" v-on:click="onShowDetail($event, props.row)" role="button">Detail &gt;&gt;&gt;</span>
          </div>
        </template>

        <template slot="username" slot-scope="props">
          <template v-if="!isAdmin">
            {{props.cell_value}}
          </template>
          <template v-else>
            <v-select :value="props.cell_value" :loading="users.loading" :options="users.dataList" label="username" v-on:input="onChangeUsername($event, props.row)"></v-select>
          </template>
        </template>

        <template slot="status" slot-scope="props">
          <v-select :value="props.cell_value" :options="statusList" v-on:input="onChangeStatus($event, props.row)"
            :class="getColorStatus(props.cell_value)"></v-select>
        </template>
      </vue-bootstrap4-table>
    </div>

    <Modal v-if="isModal" @close="isModal=false" class="my-modal">
      <template slot="header">
        <h3>
          <template v-if="!isAdmin">
            {{aTicket.name}}
          </template>
          <template v-else>
            <input :value="aTicket.name" class="form-control no-border" type="text" v-on:blur="onChangeTittle($event, aTicket)" />
          </template>
        </h3>
        <!-- <button class="btn btn-light" v-on:click="isModal=false">X</button> -->
      </template>
      <template slot="body">
        <div class="form-group row">
          <label for="assignee" class="col-sm-12 col-md-2 col-form-label text-right">Status:</label>
          <div class="col-sm-12 col-md-4">
            <v-select :value="aTicket.status" :options="statusList" v-on:input="onChangeStatus($event, aTicket)"
            :class="getColorStatus(aTicket.status)"></v-select>
          </div>
        </div>

        <div class="form-group row">
          <label for="assignee" class="col-sm-12 col-md-2 col-form-label text-right">Assignee:</label>
          <div class="col-sm-12 col-md-4">
            <template v-if="!isAdmin">
              {{aTicket.username}}
            </template>
            <template v-else>
              <v-select :value="aTicket.username" :loading="users.loading" :options="users.dataList" label="username" v-on:input="onChangeUsername($event, aTicket)"></v-select>
            </template>
          </div>
        </div>

        <div class="form-group row">
          <label for="date_of_submission" class="col-sm-12 col-md-2 col-form-label text-right">Date of submission:</label>
          <div class="col-sm-12 col-md-10 text-left">
            <template v-if="!isAdmin">
              {{aTicket.date_of_submission | moment("DD/MM/YYYY")}}
            </template>
            <template v-else>
              <date-picker v-on:change="editDateOfSubmission($event, aTicket)" type="date" format="DD/MM/YYYY">
                <template slot="input">
                  {{ aTicket.date_of_submission | moment("DD/MM/YYYY") }}
                </template>
                <template slot="icon-calendar">
                  <i class="mx-icon-calendar">
                    <svg v-if="isAdmin" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024"><path d="M940.218182 107.054545h-209.454546V46.545455h-65.163636v60.50909H363.054545V46.545455H297.890909v60.50909H83.781818c-18.618182 0-32.581818 13.963636-32.581818 32.581819v805.236363c0 18.618182 13.963636 32.581818 32.581818 32.581818h861.090909c18.618182 0 32.581818-13.963636 32.581818-32.581818V139.636364c-4.654545-18.618182-18.618182-32.581818-37.236363-32.581819zM297.890909 172.218182V232.727273h65.163636V172.218182h307.2V232.727273h65.163637V172.218182h176.872727v204.8H116.363636V172.218182h181.527273zM116.363636 912.290909V442.181818h795.927273v470.109091H116.363636z"></path></svg>
                  </i>
                </template>
              </date-picker>
            </template>
          </div>
        </div>

        <div class="form-group row">
          <label for="description" class="col-sm-12 col-md-2 col-form-label text-right">Description:</label>
          <div class="col-sm-12 col-md-10 text-left">
            <textarea :disabled="!isAdmin" :value="aTicket.description" class="form-control" v-on:blur="onChangeDescription($event, aTicket)"></textarea>
          </div>
        </div>

        <div class="form-group row">
          <label for="description" class="col-sm-6 col-md-2 col-form-label text-right">Created by:</label>
          <div class="col-sm-6 col-md-10 text-left">
            <p class="col-form-label">{{aTicket.created_by}}</p>
          </div>
        </div>
        
      </template>
    </Modal>
  </div>
</template>

<script>
import { mapActions, mapState  } from 'vuex';
import VueBootstrap4Table from 'vue-bootstrap4-table';
import DatePicker from 'vue2-datepicker';
import vSelect from "vue-select";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import Modal from '@/components/Modal.vue';

import { convertTimestamp, isRoleAdmin } from '../helper';



export default {
  name: "TicketList",
  data: function() {
    return {
        isAdmin: false,
        isModal: false,
        aTicket: {},
        statusList: ['waiting', 'working', 'issued', 'processing', 'done'],
        classes: {
            cell : {
                "w-40 ticket-name" : function (row, column) {
                  return column && column.name === 'name';
                },
                "w-20" : function (row, column) {
                  return column.name !== 'name';
                },
            }
        },
        actions: [
            {
                btn_text: "+ Add task",
                event_name: "on-add-task",
                class: "btn btn-primary my-custom-class",
            }
        ],
        columns: [
          // {
          //   label: "Show/Hide",
          //   name: "isShow",
          //   visibility: isAdmin,
          // },
          {
            label: "Title",
            name: "name",
            sort: true,
          },
          // {
          //   label: "Description",
          //   name: "description",
          //   sort: true,
          //   visibility: false,
          // },
          {
            label: "Assignee",
            name: "username",
            sort: true,
          },
          {
            label: "Due date",
            name: "date_of_submission",
            sort: true,
          },
          {
            label: "Status",
            name: "status",
            sort: true,
          },
        ],
        config: {
          card_mode: false,
          pagination: false,
          pagination_info: false,
          show_refresh_button: false,
          show_reset_button: false,
          global_search: {
            placeholder: "Search...",
          },
        }
    };
  },
  components: {
    VueBootstrap4Table,
    DatePicker,
    vSelect,
    Modal,
    Loading,
  },
  created: function () {
    const isAdmin = isRoleAdmin();
    this.isAdmin = isAdmin;
    this.getTicketList();
    this.getUserList();
	},
  computed: {
    ...mapState(['tickets', 'users']),
  },
  methods: {
    ...mapActions(['getTicketList', 'updateTicket', 'updateTicketStatus', 'getUserList']),
    getColorStatus: function (value) {
      return 'v-selected-' + value;
    },
    fetchData: function(cloneRow) {
      const index = this.tickets.dataList.findIndex(item => item.id === cloneRow.id);
      this.tickets.dataList.splice(index, 1, cloneRow);
    },
    onShowDetail: function (e, row) {
      e.preventDefault();
      this.isModal = true;
      this.aTicket = Object.assign({}, this.aTicket, row);
    },
    onChangeTittle: function (event, row) {
      const { id, name, description, assign_user_id, status, isShow, date_of_submission } = row;
      const value = event.target.value;
      if (name !== value) {
        this.updateTicket({id, description, assign_user_id, status, isShow, date_of_submission, name: value });
        row.name = value;
        this.fetchData(row);
      }
    },
    onChangeDescription: function (event, row) {
      const { id, name, description, assign_user_id, status, isShow, date_of_submission } = row;
      const value = event.target.value;
      if (description !== value) {
        this.updateTicket({id, name, assign_user_id, status, isShow, date_of_submission, description: value });
        row.description = value;
        this.fetchData(row);
      }
    },
    onChangeUsername: function (event, row) {
      const { id, name, description, assign_user_id, status, isShow, date_of_submission } = row;
      console.log(event);
      const value = event.id;
      if (assign_user_id !== value) {
        this.updateTicket({id, name, description, status, isShow, date_of_submission, assign_user_id: value });
        row.assign_user_id = event.id;
        row.username = event.username;
        row.email = event.email;
        row.role = event.role;
        this.fetchData(row);
      }
    },
    onChangeStatus: function (event, row) {
      const { id, status } = row;
      console.log(event);
      if (status !== event) {
        this.updateTicketStatus({id, status: event });
        row.status = event;
        this.fetchData(row);
      }
    },
    onUpdateIsShow: function (e, row) {
      e.preventDefault();
      e.stopPropagation();
      const { id, name, description, assign_user_id, status, isShow, date_of_submission } = row;
      const _isShow = isShow === 1 ? 0 : 1;
      this.updateTicket({id, name, description, assign_user_id, status, date_of_submission, isShow:_isShow });

      row.isShow = _isShow;
      this.fetchData(row);
    },
    editDateOfSubmission: function (event, row) {
      const cloneRow = row;
      const { id, name, description, assign_user_id, status, isShow, date_of_submission } = row;
      const value = convertTimestamp(event);

      if (date_of_submission !== value) {
        this.updateTicket({id, name, description, assign_user_id, status, isShow, date_of_submission: value });
        // Array.isArray(this.tickets.dataList) && this.tickets.dataList.forEach((el, index) => {
        //   if (el.id === id) {
        //     this.tickets.dataList[index].date_of_submission = date_of_submission;
        //     return;
        //   }
        // });
        // const index = this.tickets.dataList.findIndex(item => item.id === id);
        // this.tickets.dataList[index].date_of_submission = date_of_submission;
        // this.getTicketList();
        cloneRow.date_of_submission = value;
        this.fetchData(cloneRow);
      }
    },
    onAddTask: function () {
      this.$router.push('/ticket/create');

      console.log();
    }
  }
};
</script>

<style lang="scss">
.ticket-list-page {
  .w-40 {
    width: 40% !important;
  }

  .w-20 {
    width: 20% !important;
  }

  .v-selected-waiting {
    .vs__dropdown-toggle {
      background-color: yellow !important;
      .vs__selected {
        color: green;
      }
    }
  }

  .v-selected-working {
    .vs__dropdown-toggle {
      background-color: blue !important;
      .vs__selected {
        color: white;
      }
    }
  }

  .v-selected-issued {
    .vs__dropdown-toggle {
      background-color: red !important;
      .vs__selected {
        color: white;
      }
    }
  }

  .v-selected-processing {
    .vs__dropdown-toggle {
      background-color: teal !important;
      .vs__selected {
        color: white;
      }
    }
  }

  .v-selected-done {
    .vs__dropdown-toggle {
      background-color: green !important;
      .vs__selected {
        color: white;
      }
    }
  }

  .vs--disabled {
    .vs__dropdown-toggle {
      border: none;
      background: none;

      .vs__selected-options {
        justify-content: center;
      }

      .vs__selected {
        cursor: default;
      }

      .vs__actions, .vs__search {
        display: none;
      }
    }
  }

  .my-modal {
    h3 {
      width: 100%;
      input.no-border {
        border: none;
        background: none;
      }
    }
    

    textarea.form-control {
      border: none;
    }

    .mx-datepicker {
      margin-top: 8px;
    }
  }
}

</style>