<template>
	<div class="signin-page card card-body bg-light container">
		<h2>Sign In</h2>
		<form @submit.prevent="handleSubmit" class="justify-content-md-center">
			<div class="form-group" v-if="status.error && status.error.message">
				<p class="text-danger">{{status.error.message ? status.error.message : '' }}</p>
			</div>
			<div class="form-group row">
				<label for="email" class="col-sm-3 col-md-4 col-form-label text-right">Email</label>
				<div class="col-sm-9 col-md-4">
					<input type="email" v-model="email" id="email" name="email" class="form-control" :class="{ 'is-invalid': submitted && !email }" />
					<div v-show="submitted && !email" class="invalid-feedback">Email is required</div>
				</div>
			</div>
			<div class="form-group row">
				<label for="password" class="col-sm-3 col-md-4 col-form-label text-right">Password</label>
				<div class="col-sm-9 col-md-4">
					<input type="password" v-model="password" id="password" name="password" class="form-control" :class="{ 'is-invalid': submitted && !password }" />
					<div v-show="submitted && !password" class="invalid-feedback">Password is required</div>
				</div>
			</div>
			<div class="form-group">
				<button class="btn btn-primary">Sign in</button>
			</div>
		</form>
	</div>
</template>

<script>
import { mapActions, mapState } from 'vuex';

export default {
    name: 'Signin',
	data () {
		return {
			email: '',
			password: '',
			submitted: false,
		}
	},
	computed: {
		...mapState(['status'])
	},
	created: function () {
		const user = JSON.parse(localStorage.getItem('user'));
		if (user) this.signout();
	},
	methods: {
		...mapActions(['signin', 'signout']),
		handleSubmit (e) {
			e.preventDefault();
			this.submitted = true;
			const { email, password } = this;
			if (email && password) {
				this.signin({ email, password });
			}
		}
	}
}
</script>
